export class Solution {
  div: HTMLElement;
  renderer: RenderWireframe;
  pts = [];
  t = 0.0;
  constructor(id: string) {
    this.div = document.getElementById(id);

    this.renderer = new RenderWireframe(id);

    this.pts = genGridPt();
    this.renderer.pts.push(...this.pts);
    this.renderer.render();
    this.upadte();
  }
  public upadte() {
    requestAnimationFrame(() => {
      this.upadte();
    });

    for (let i = 0; i < this.pts.length; ++i) {
      this.pts[i].z += Math.cos((this.pts[i].x + this.pts[i].y) * this.t) * 2;
    }

    this.t += 0.001;
  }
  public destroy() {
    // this.renderer.destroy();
    try {
      while (this.div && this.div.lastElementChild) {
        const lastChild = this.div.lastElementChild as Node;
        this.div.removeChild(lastChild);
      }
    } catch (error) {
      console.error("Error removing child elements:", error);
    }
  }
}

const genRandomPt = (numPoints = 100) => {
  const pts = [];
  for (let i = 0; i < numPoints; i++) {
    pts.push({
      x: (Math.random() - 0.5) * 500,
      y: (Math.random() - 0.5) * 500,
      z: (Math.random() - 0.5) * 500,
    });
  }
  return pts;
};

const genGridPt = (numPoints = 5) => {
  const scale = 10;
  const pts = [];
  for (let j = -numPoints; j < numPoints; j++) {
    for (let i = -numPoints; i < numPoints; i++) {
      pts.push({
        x: i * scale,
        y: j * scale, // Z-axis is up, so keep Y constant (ground plane)
        z: 0,
      });
    }
  }
  return pts;
};

class RenderWireframe {
  pts = [];
  constructor(id) {
    const div = document.getElementById(id);
    const bounding = div.getBoundingClientRect();

    const canvas = document.createElement("canvas");
    canvas.width = bounding.width;
    canvas.height = bounding.height;
    div.appendChild(canvas);

    const ctx = canvas.getContext("2d");

    let scale = 1;
    let panX = 0,
      panY = 0;
    let isPanning = false;
    let startX = 0,
      startY = 0;
    const zoomSpeed = 0.05;

    canvas.addEventListener("wheel", (e) => {
      e.preventDefault();

      const mouseX = e.offsetX;
      const mouseY = e.offsetY;
      const delta = e.deltaY > 0 ? -1 : 1;

      const zoomFactor = 1 + delta * zoomSpeed;

      panX = mouseX - zoomFactor * (mouseX - panX);
      panY = mouseY - zoomFactor * (mouseY - panY);

      // Apply zoom
      scale *= zoomFactor;
      rendering();
    });

    canvas.addEventListener("mousedown", (e) => {
      isPanning = true;
      startX = e.clientX - panX;
      startY = e.clientY - panY;
    });

    canvas.addEventListener("mousemove", (e) => {
      if (isPanning) {
        panX = e.clientX - startX;
        panY = e.clientY - startY;
        rendering();
      }
    });

    canvas.addEventListener("mouseup", () => {
      isPanning = false;
    });

    canvas.addEventListener("mouseout", () => {
      isPanning = false;
    });

    // Helper to draw shapes
    const rendering = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      ctx.save();
      ctx.translate(panX, panY);
      ctx.scale(scale, scale);

      // console.log(this.pts)
      this.pts.forEach((p)=> {
        // console.log(p)
        ctx.beginPath();
        ctx.arc(p.x, p.y, 5, 0, Math.PI * 2);
        ctx.fillStyle = "blue";
        ctx.fill();
        ctx.closePath();
        ctx.strokeStyle = "#f0f";
        ctx.lineWidth = 2;
        ctx.stroke();
      })

      // Draw a circle
      ctx.beginPath();
      ctx.arc(200, 200, 50, 0, Math.PI * 2);
      ctx.fillStyle = "blue";
      ctx.fill();
      ctx.closePath();
      ctx.strokeStyle = "#0f0";
      ctx.lineWidth = 2;
      ctx.stroke();

      // Draw a rectangle
      ctx.beginPath();
      ctx.rect(400, 300, 150, 100);
      ctx.fillStyle = "red";
      ctx.fill();
      ctx.closePath();

      ctx.restore();





    };

    rendering();
    this.render = () => {
      rendering();
    }
  }
  public render() {

  }
}
